<template>
    <div>
      
      <div class="tile">
            <div class="tile is-parent is-vertical">
              <article class="tile is-child notification is-primary">
                <p class="title">{{vehiclesTitle}} si {{fuelsTitle}} </p>
                <p class="subtitle">
                      <button  class="button is-small" @click="addVehicle">+ autovehicul</button> &nbsp;
                      <button class="button is-small" @click="addFuel">+ carburant</button></p>
              </article>
            </div>
      </div>

<DashItem v-for="v in dashboard" :vehicle="v" :key="v.id"/>

<div class="is-size-3" v-show="dashboard && dashboard.length == 0">Nu ai nici o inregistrare. Adauga vehicule si bonuri de consum pentru avedea graficele</div>


    </div>
</template>

<script>

import VehicleForm from "@/components/VehicleForm"
import FuelForm from "@/components/FuelForm"
import DashItem from "@/components/DashItem"

import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'dashboard',
  components:{DashItem},
   data () {
            return {
                data1: [
                {
                    chartType: 'line',
                    values: [5, 6, 3.7, 15, 8, 5, 7, 3]
                }],
                 data2: [
                {
                    chartType: 'line',
                    values: [8, 6, 7, 8, 8, 5, 7, 7]
                }]
            }
        },
        computed:{
          ...mapGetters(['vehicles','fuels', 'dashboard']),
          fuelsTitle(){
            return this.fuels && this.fuels.length == 1 ?
              "1 bon carburant": `${this.fuels.length} bonuri` + ` carburant`
          },
          vehiclesTitle(){
            return this.vehicles && this.vehicles.length == 1 ?
              "1 autovehicul": `${this.vehicles.length} autovehicule`
          }
        },
        methods:{
          ...mapActions(['loadDashboard']),
           addVehicle: function() {
                this.$modal.open({
                    parent: this,
                    component: VehicleForm,
                    hasModalCard: true
                });
            },
            addFuel: function() {
                this.$modal.open({
                    parent: this,
                    component: FuelForm,
                    hasModalCard: true
                });
            }
        },
        created(){this.loadDashboard()}
}
</script>

<style>
.card{
  margin-bottom: 45px;
}
</style>
