import Vue from 'vue'
import Vuex from 'vuex'

import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    phone:'',
    fTypes: [],
    vehicles:[],
    fuels:[],
    dashboard : []
  },
  getters:{
    fuelTypes: (store) => store.fTypes,
    vehicles:(store) => store.vehicles,
    fuels:(store) => store.fuels,
    currentPhone:(store) => store.phone,
    isAuthenticated:(store) => store.phone && store.phone.length > 0 ,
    dashboard:(store) => store.dashboard,

  },
  mutations: {
    SET_PHONE:(store, phone)=> store.phone =  phone,
    SET_TYPES: (store, types) => {
       store.fTypes = types
    },
    SET_VEHICLES: (store, vehicles) => store.vehicles = vehicles,
    SET_FUELS: (store, fuels) => store.fuels = fuels,
    SET_DASHBOARD: (store, dash) => store.dashboard = dash
  },
  actions: {
    setPhone({commit,dispatch}, phone){
      Vue.axios.defaults.headers.common["phone"] = phone;
     commit("SET_PHONE",phone);
     dispatch("loadFuels");
     dispatch("loadVehicles");
    },
    logout({commit}){
      commit("SET_PHONE","");
      commit("SET_VEHICLES",[]);
      commit("SET_FUELS",[]);
    },
    loadInitial({commit}) {
      Vue.axios.get('/api/types')
        .then(resp => commit("SET_TYPES", resp.data))
        .catch(err => console.error(err))
    },
    loadVehicles({commit}) {
      Vue.axios.get("/api/vehicles")
        .then((resp) => commit("SET_VEHICLES", resp.data))
        .catch((err) => console.error(err));
    },
    loadDashboard({commit}) {
      Vue.axios.get("/api/dashboard")
        .then((resp) => {
          
          
          var dta = resp.data.map((x) =>{
              var obj  =  {items :{}};
              console.log(x);
              obj.id = x.vehicle.id;
              obj.name = x.vehicle.name;
              obj.hasData = (x.fuels || []).length > 0;
                obj.fuels = (x.fuels || []).map((f) =>{
                    var o = {};
                    obj.items[f.date] = f.consumption;
                    return o;
                } );
                return obj;
              });

        
          console.dir(dta);


          commit("SET_DASHBOARD", dta);
        })
        .catch((err) => console.error(err));
    },
    saveVehicle({commit}, vehicle) {
      return Vue.axios.post("/api/vehicles", vehicle);
    },
    delVehicle({commit}, id){
      return Vue.axios.delete(`/api/vehicles/${id}`);
    }, 
    saveFuel:(context, fuel) =>{
        return  Vue.axios.post(`/api/vehicles/${fuel.vehicleId}/fuels`, fuel); 
    },
    loadFuels({commit}) {
      Vue.axios.get("/api/fuels")
        .then((resp) => commit("SET_FUELS", resp.data))
        .catch((err) => console.error(err));
    },
    delFuel({commit}, id){
      return Vue.axios.delete(`/api/fuels/${id}`);
    }
  }
})
